import { connect } from "react-redux";
import Form from "./Form";
import { Dispatch } from "redux";
import { Match } from "domain/match";
import {
  operations,
  MatchActionTypes,
  MatchState,
  selectors,
} from "store/ducks/match/match";
import { AppState } from "store";

interface DispatchProps {
  onSubmit(match: Match): void;
  onFinish(): void;
}

const mapStateToProps = (state: AppState): MatchState => ({
  status: selectors.getMatchStatus(state),
});

const mapDispatchToProps = (dispatch: Dispatch): DispatchProps => ({
  onSubmit: (match: Match): MatchActionTypes =>
    dispatch(operations.endMatch(match)),
  onFinish: (): MatchActionTypes => dispatch(operations.startMatch()),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(Form);
