import React, { Component, ReactNode, FormEvent } from "react";
import { css } from "@emotion/core";
import { Match } from "domain/match";
import { MatchStatus, matchStatuses } from "store/ducks/match/match";

interface FormProps {
  onSubmit(match: Match): void;
  onFinish(): void;
  status: MatchStatus;
}

interface FormState {
  [key: string]: string;
  firstPlace: string;
  secondPlace: string;
  thirdPlace: string;
  fourthPlace: string;
}

const formBase = css({
  maxWidth: "22em",
  margin: "0 auto",
});

const centered = css({
  textAlign: "center",
});

export default class Form extends Component<FormProps, FormState> {
  state = {
    firstPlace: "",
    secondPlace: "",
    thirdPlace: "",
    fourthPlace: "",
  };

  handleInputChange = (event: FormEvent): void => {
    const target = event.target as HTMLInputElement;
    const value = target.value;
    const name = target.name;

    this.setState({
      [name]: value,
    });
  };

  handleSubmit = (event: FormEvent): void => {
    const { onSubmit } = this.props;
    const { firstPlace, secondPlace, thirdPlace, fourthPlace } = this.state;
    event.preventDefault();
    const obj: Match = {
      firstPlace: parseInt(firstPlace, 10),
      secondPlace: parseInt(secondPlace, 10),
    };
    if (thirdPlace !== "") {
      obj.thirdPlace = parseInt(thirdPlace, 10);
    }
    if (fourthPlace !== "") {
      obj.fourthPlace = parseInt(fourthPlace, 10);
    }
    onSubmit(obj);
    this.setState({
      firstPlace: "",
      secondPlace: "",
      thirdPlace: "",
      fourthPlace: "",
    });
  };

  render(): ReactNode {
    const { firstPlace, secondPlace, thirdPlace, fourthPlace } = this.state;
    const { status, onFinish } = this.props;
    if (status === matchStatuses.FINISHING) {
      return <p>Processando...</p>;
    }

    if (status === matchStatuses.FINISHED) {
      return (
        <div css={[formBase, centered]}>
          <p>Pontuação gravada com sucesso</p>
          <button onClick={onFinish}>Nova partida</button>
        </div>
      );
    }

    return (
      <form css={formBase} onSubmit={this.handleSubmit}>
        <fieldset>
          <legend>Colocação da partida</legend>
          <ul>
            <li>
              <label htmlFor="firstPlace">Primeiro colocado:</label>
              <input
                type="tel"
                id="firstPlace"
                name="firstPlace"
                value={firstPlace}
                onChange={this.handleInputChange}
              />
            </li>
            <li>
              <label htmlFor="secondPlace">Segundo colocado:</label>
              <input
                type="tel"
                id="secondPlace"
                name="secondPlace"
                value={secondPlace}
                onChange={this.handleInputChange}
              />
            </li>
            <li>
              <label htmlFor="thirdPlace">Terceiro colocado:</label>
              <input
                type="tel"
                id="thirdPlace"
                name="thirdPlace"
                value={thirdPlace}
                onChange={this.handleInputChange}
              />
            </li>
            <li>
              <label htmlFor="fourthPlace">Quarto colocado:</label>
              <input
                type="tel"
                id="fourthPlace"
                name="fourthPlace"
                value={fourthPlace}
                onChange={this.handleInputChange}
              />
            </li>
          </ul>
          <button type="submit">Enviar</button>
        </fieldset>
      </form>
    );
  }
}
