import { cps, select, put, takeLatest } from "redux-saga/effects";
import { Match } from "domain/match";
import { Player } from "domain/player";
import { MATCH, GLOBAL } from "../actionTypes";
import { operations as playerOperations } from "../player/player";
import { endMatch as endMatchUseCase } from "app/match/endMatch";
import { playerSelectors } from "../player";
import { GlobalActionTypes } from "../global/global";

/* eslint-disable @typescript-eslint/no-explicit-any */
function applyNodeCallbacks<T extends (...arg: any[]) => any>(fn: T) {
  return (...arg: Parameters<T>): void => {
    const fnArgs = arg;
    const callback = arg.pop();
    const callbacks = {
      onSuccess: (data: any): void => {
        callback(null, data);
      },
      onError: (error: any): void => {
        callback(error);
      },
    };

    return fn(...fnArgs, callbacks);
  };
}
/* eslint-enable @typescript-eslint/no-explicit-any */

export const matchStatuses = {
  START: "START",
  FINISHING: "FINISHING",
  FINISHED: "FINISHED",
};

export type MatchStatus = keyof typeof matchStatuses;

export interface MatchState {
  status: string;
}

interface EndMatch {
  type: typeof MATCH.END;
  payload: {
    match: Match;
  };
}

interface EndMatchSuccess {
  type: typeof MATCH.END_SUCCESS;
}

interface StartMatch {
  type: typeof MATCH.START;
}

export type MatchActionTypes = EndMatch | EndMatchSuccess | StartMatch;

const endMatch = (match: Match): MatchActionTypes => ({
  type: MATCH.END,
  payload: {
    match,
  },
});

const endMatchSuccess = (): MatchActionTypes => ({
  type: MATCH.END_SUCCESS,
});

const startMatch = (): MatchActionTypes => ({
  type: MATCH.START,
});

// eslint-disable-next-line @typescript-eslint/explicit-function-return-type
function* endMatchSaga({ payload: { match } }: EndMatch) {
  const playerEntities = yield select(playerSelectors.getPlayerEntity);

  const players: Player[] = Object.values(match)
    .map((id: number): Player => playerEntities[id])
    .filter((player: Player | undefined) => player != null);

  try {
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    const updatedPlayers: Player[] = yield (cps as any)(
      applyNodeCallbacks(endMatchUseCase),
      match,
      players,
    );

    yield put(playerOperations.updatePlayer(updatedPlayers));
    yield put(endMatchSuccess());
  } catch (e) {
    console.log(e);
  }
}

function* watchEndMatchSaga(): Generator {
  yield takeLatest(MATCH.END, endMatchSaga);
}

export const operations = {
  watchEndMatchSaga,
  endMatch,
  startMatch,
};

const getMatchStatus = (state: any): MatchStatus => state.match.status;

export const selectors = {
  getMatchStatus,
};

const initialState = {
  status: matchStatuses.START,
};

export const reducer = (
  state = initialState,
  action: MatchActionTypes | GlobalActionTypes,
): MatchState => {
  switch (action.type) {
    case MATCH.END:
      return {
        status: matchStatuses.FINISHING,
      };
    case MATCH.END_SUCCESS:
      return {
        status: matchStatuses.FINISHED,
      };
    case MATCH.START:
      return {
        status: matchStatuses.START,
      };
    case GLOBAL.RESET:
      return initialState;
    default:
      return state;
  }
};
